import { type ClassValue, clsx } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export function formatDate(date: Date | string): string {
  return new Date(date).toLocaleDateString("id-ID", {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  });
}

export function formatNIK(nik: string): string {
  if (!nik) return "";
  // Format: 1234-5678-9012-3456
  return nik.replace(/(\d{4})(\d{4})(\d{4})(\d{4})/, "$1-$2-$3-$4");
}

export function validateNIK(nik: string): boolean {
  // NIK should be 16 digits
  return /^\d{16}$/.test(nik.replace(/[^0-9]/g, ""));
}

export function calculateAverageRating(ratings: number[]): number {
  if (ratings.length === 0) return 0;
  const sum = ratings.reduce((acc, curr) => acc + curr, 0);
  return Number((sum / ratings.length).toFixed(1));
}

export function getRatingLabel(rating: number): string {
  switch (rating) {
    case 1:
      return "Sangat Tidak Puas";
    case 2:
      return "Tidak Puas";
    case 3:
      return "Cukup";
    case 4:
      return "Puas";
    case 5:
      return "Sangat Puas";
    default:
      return "";
  }
}

export function calculateRatingDistribution(ratings: number[]): Record<number, number> {
  return ratings.reduce((acc, rating) => {
    acc[rating] = (acc[rating] || 0) + 1;
    return acc;
  }, {} as Record<number, number>);
}

export function calculatePercentage(value: number, total: number): string {
  if (total === 0) return "0%";
  return `${((value / total) * 100).toFixed(1)}%`;
}

export function formatTimer(seconds: number): string {
  const minutes = Math.floor(seconds / 60);
  const remainingSeconds = seconds % 60;
  return `${String(minutes).padStart(2, "0")}:${String(remainingSeconds).padStart(2, "0")}`;
}

export interface SurveyResponse {
  id: number;
  fullname: string;
  nik: string;
  service_area: string;
  service_type: string;
  rating: number;
  submitted_at: string;
}

export interface SurveySettings {
  require_fullname: boolean;
  require_nik: boolean;
  timer_duration: number;
  service_areas: string[];
  service_types: Record<string, string[]>;
}

export interface RatingStats {
  total: number;
  average: number;
  distribution: Record<number, number>;
  byServiceArea: {
    area: string;
    count: number;
    average: number;
  }[];
}

export function calculateStats(responses: SurveyResponse[]): RatingStats {
  const total = responses.length;
  const ratings = responses.map(r => r.rating);
  const average = calculateAverageRating(ratings);
  const distribution = calculateRatingDistribution(ratings);

  const byServiceArea = Object.entries(
    responses.reduce((acc, r) => {
      if (!acc[r.service_area]) {
        acc[r.service_area] = { count: 0, total: 0 };
      }
      acc[r.service_area].count++;
      acc[r.service_area].total += r.rating;
      return acc;
    }, {} as Record<string, { count: number; total: number; }>)
  ).map(([area, stats]) => ({
    area,
    count: stats.count,
    average: Number((stats.total / stats.count).toFixed(1))
  }));

  return {
    total,
    average,
    distribution,
    byServiceArea
  };
}

export function isValidServiceArea(area: string, settings: SurveySettings): boolean {
  return settings.service_areas.includes(area);
}

export function isValidServiceType(area: string, type: string, settings: SurveySettings): boolean {
  return settings.service_types[area]?.includes(type) ?? false;
}

export function sanitizeInput(input: string): string {
  return input.trim().replace(/[<>]/g, "");
}
